.login_page {
  max-width: 500px;
  margin: auto;
  color: #444;
  padding: 0 15px;
}

.login_page h2 {
  text-transform: uppercase;
  font-size: 2rem;
  margin: 20px 0;
  letter-spacing: 1.5px;
}

.login_page input {
  width: 100%;
  height: 45px;
  margin: 10px 0;
  border: 2px solid #777;
  outline: none;
  padding: 0 10px;
}

.login_page .row {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap-reverse;
  align-items: flex-start;
}

.login_page .row a {
  color: crimson;
}

.login_page .row button {
  width: 150px;
  padding: 10px 0;
  color: white;
  background-color: black;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  margin-right: 10px;
}

.login_page p {
  margin: 10px 0;
}

.login_page a {
  color: #111;
  font-weight: 600;
}

/* ------------------Forgot Password-------------- */
.forgot-password h2 {
  color: #555;
  text-transform: uppercase;
  text-align: center;
  font-size: 2rem;
  margin: 50px 0;
  letter-spacing: 1.3px;
}

.forgot-password .row {
  max-width: 500px;
  margin: auto;
  padding: 0 10px;
}

.forgot-password .row input {
  width: 100%;
  height: 45px;
  border: 1px solid #ccc;
  outline: none;
  padding: 0 15px;
  border-radius: 3px;
  margin: 5px 0;
}

.forgot-password .row button {
  background-color: #333;
  color: #ffff;
  padding: 10px 30px;
  text-transform: uppercase;
  letter-spacing: 1.3px;
  border-radius: 3px;
  margin-top: 15px;
}
